<template>
	<div class="reg">
		<Error/>
		<div class="login__welcome">
			<h2>注册</h2>
			<p>欢迎来到七彩蛋糕！{{errormess}}</p>
		</div>
		<Input placeholder='请输入手机号码' v-model='username'/>
		<Input placeholder='请输入密码' text='password' v-model='password'/>
		<LoginButton loginCon='注册' @click='reg'/>
		<router-link class="Login__p" to='/login'>登录</router-link>
		<p class="login__concat">客服电话：400-658-1177</p>
	</div>
</template>

<script>
	import Error from '../components/Error.vue'
	import Input from '../components/Input.vue'
	import LoginButton from '../components/LoginButton.vue'
	import Icon from '../components/Icon.vue'
	import axios from 'axios'
	export default{
		name:'Reg',
		data(){
			return{
				username:'',
				password:'',
				errormess:''
			}
		},
		methods:{
			reg(){
				axios({
					  url:'/api/reg',
					  method:'post',
					  data:{
						  username:this.username,
						  password:this.password
					  }
					}).then(
					  res => {
						  // console.log(res)
						if(res.data.err===0){
						  this.$router.replace('/login')
						}else{
						  this.errorMess=res.data.msg
						}
					  }
					)
				}
		},
		components:{
			Error,Input,LoginButton,Icon
		}
	}
</script>

<style>
	.login__welcome{
		padding-left: .84rem;
	}
	.login__welcome h2{
		font-size: .5rem;
		color: #333333;
		margin-bottom: .5rem;
	}
	.login__welcome p{
		font-size: .28rem;
		color: #9c9c9c;
		margin-bottom: 1.1rem;
	}
	.LoginButton{
		margin-top: .5rem;
		margin-bottom: .4rem;
	}
	.Login__p{
		display: block;
		font-size: .26rem;
		color: #9c9c9c;
		text-align: center;
		margin-bottom: 1.3rem;
	}
	
	.login__concat{
		text-align: center;
		font-size: .26rem;
		color: #b3b3b3;
		margin-bottom: 2rem;
	}
</style>
