<template>
	<div class="Cake__areas" @click="goDetail">
		<img :src="data.img" alt="">
		<div class="Cake__content">
			<h3>{{data.h3Title}}</h3>
			<p>{{data.title}}</p>
			<a href=""><b>￥{{data.price}}</b><span>爆款</span></a>
		</div>
	</div>
</template>

<script>
	export default{
		name:'Cake',
		props:{
		      data:{
		        type:Object,
		        required:true,
		        default:function(){ //props类型是个对象，默认值要是一个函数，最后需要返回对象
		          //...
		          return {}
		        }
		      },
			  apiName:{
				  type:String,
				  default:null
				},
			 index:{
				type:Number,
				default:null
			  },
			},
			methods:{
				goDetail(){
					if(this.apiName){
						this.$router.push({path:'/detail/${this.index}',query:{index:this.index}})
						// console.log(this.index)
					}
				}
			}
	}
</script>

<style>
	.Cake__areas{
		width: 46%;
