		border-radius: 3%;
		margin-left: 1%;
		margin-right: 1%;
	}
	.Cake__content{
		padding:0 .3rem;
	}
	.Cake__areas img{
		width: 100%;
		height: 3.67rem;
	}
	.Cake__areas h3{
		font-size: .26rem;
		color: #343434;
		margin-top: .25rem;
		margin-bottom: .15rem;
	}
	.Cake__areas p{
		font-size: .22rem;
