<template>
	<div class="type">
		<div v-for="(item,index) in typeCakedata" :key="item.id" :index=index>
			<div class="typeTitle">
				<span></span>
				<p>{{item.type}}</p>
				<span></span>
			</div>
			<div class="typeCake__content">
				<div class="typeCake" v-for="(list,index) in item.typeCake" :key="list.id" :index=index>
					<img :src="list.img" alt="">
					<p>{{list.title}}</p>
				</div>
			</div>
			
		</div>
		
	</div>
	
</template>

<script>
	export default{
		data(){
			return {
				typeCakedata:{}
			}
		},
		methods:{
			getCakeType(){
				axios({
					url:'/data/type.json'
				}).then(res=>{
					this.typeCakedata = res.data
				})
			}
		},
		mounted() {
			this.getCakeType()
		}
	}
</script>

<style>
	.typeCake__content{
		display: flex;
		justify-content: space-around;
		flex-wrap: wrap;
	}
	.typeTitle{
		display: flex;
		justify-content: center;
		align-items: center;
		margin:.6rem 0;
	}
	.typeTitle span{
		width: .6rem;
		height: .03rem;
		background-color: #cfcfcf;
	}
	.typeTitle p{
		font-size: .3rem;
		color: #646464;
		margin:0 .6rem;
	}
	.typeCake img{
		width: 1.3rem;
		height: 1.2rem;
	}
	.typeCake p{
		font-size: .22rem;
		color: #676767;
		text-align: center;
		margin-top: .3rem;
		margin-bottom: .6rem;
	}
</style>
