<template>
	<div class="input">
		<input :type="text" :placeholder="placeholder" @input="$emit('input',$event.target.value)">
	</div>
</template>

<script>
	export default{
		props:{
			text:{
				type:String,
				default:'text'
			},
			placeholder:{
				type:String,
				default:'请输入手机号码'
			},
		}
	}
</script>

