import axios from 'axios'
import router from './router.js'
import Vue from 'vue'
import store from './vuex.js'
//添加一个请求的拦截器
axios.interceptors.request.use(function(config){
	// let token = window.localStorage.getItem('user')
	// token = token?JSON.parse(window.localStorage.getItem('user')).token:'',
	// config.header={
	// 	token:token
	// }
	// console.log(store.state.user)
	// 请求发出之前做点事  请求体里面 每次都自动携带token
	  let token = store.state.user.token;
	  token = token ? token : '';
	  config.headers={
	    token
	  }
	store.commit('UPDATE_LOADING',true)
	return config
},function(error){
	return Promise.reject(error)
})

//添加一个响应的拦截器
axios.interceptors.response.use(function(response){
	let currentRoute = router.currentRoute.fullPath;
	if(response.data.err===2&&!currentRoute.includes('/login')){
		router.replace({
			path:'/login',
			query:{p:currentRoute}
		})
	}
	store.commit('UPDATE_LOADING',false)
	return response
},function(error){
	return Promise.reject(error)
})
Vue.prototype.$axios=axios;
window.axios=axios;
export default axios;