<template>
  <div id="app">
	  <Loading v-show="$store.state.bLoading"/>
	   <transition
		enter-active-class = "animated bounceInLeft"
		leave-active-class = "animated bounceOutRight"
	  >
	    <keep-alive>
			<router-view></router-view>
		</keep-alive>
	</transition>
	
	<Footer v-show="$store.state.bFoot"/>
  </div>
</template>

<script>

import Loading from '../components/Loading.vue'
import Footer from './Footer.vue'

export default {
  name: 'App',
  components: {
	  
	  Loading,Footer
  },
  watch:{
    	$route:{//路由监听，属性检测
        handler(nextValue,PrevValue){
          // console.log('路由变化了',nextValue);
          
          let path = nextValue.path;
          
          if(/car|type|home|mine/.test(path)){//home follow column
            this.$store.commit('UPDATE_FOOT',true);
          }
          
          if(/detail|login|reg/.test(path)){//detial login reg
            this.$store.commit('UPDATE_FOOT',false);
          }
          
        },
        immediate:true
      }
    }
}
</script>

