<template>
	<div class="home-content">
		<div class="home-header">
			<span class="iconfont icon-sousuo-m"></span>
			<img class="home__img" src="/img/logo.png" alt="">
			<span class="iconfont icon-jianting"></span>
		</div>
		<div class="Home__content">
			<Banner/>
			<NavList/>
			<NavHome/>
			<div class="Home__button">
				<Button/>
				<Button bgcolor='#9aa6e0' content='在线客服'/>
			</div>
			<div class="Home__cakecontent">
				<CakeTitle/>
				<div class="home_cake">
					<Cake 
						v-for="(item,index) of data"
					      :key="item.id"
					      :data="item"
					      :index="index"
						  api-name="home"
					 />
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	import Banner from '../components/Banner.vue'
	import NavList from '../components/NavList.vue'
	import NavHome from '../components/NavHome.vue'
	import Button from '../components/Button.vue'
	import CakeTitle from '../components/CakeTitle.vue'
	import Cake from '../components/Cake.vue'
	// import axios from 'axios'
	export default{
		name:'Home',
		data(){
			return {
				data:{}
			}
		},
		components:{
			Banner,NavList,NavHome,Button,CakeTitle,Cake
		},
		mounted(){
			this.getdata()
		},
		methods:{
			getdata(){
				axios({
					url:'/data/detail.json',
				}).then(res=>{
					this.data = res.data
				})
			}
		},
	}
</script>

<style>
	.home-header{
		width: 100%;
		height: 1rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.home-header span{
		font-size:.4rem;
	}
	.icon-sousuo-m{
		margin-left: .34rem;
	}
	.icon-jianting{
		margin-right: .34rem;
	}
	.home__img{
		width:1.82rem;
		height:.52rem;
	}
	.Home__content{
		flex: 1;
		overflow: auto;
	}
	.Home__button{
		display: flex;
		justify-content: space-around;
	}
	.Home__cakecontent{
		background: #f8f8f8;
		overflow: hidden;
		margin-bottom: 1rem;
	}
	.home_cake{
		display: flex;
		justify-content: space-around;
		flex-wrap: wrap;
	}
</style>
