const state={
	car:[]
}


const actions={
	addItem:({commit,state},payload)=>{
		let arr=[...state.car]
		let find = false;
		arr.forEach((item,index)=>{
			if(item.id==payload.id){
				item.number++
				find=true
			}
		})
		if(!find){
			payload.number=1
			arr.push(payload)
		}
		commit('ADD_ITEM',arr)
	},
	changeItem:({commit,state},payload)=>{
		state.car.forEach((item,index)=>{
			if(item.id==payload.id){
				item.number+=payload.num
			}
		}),
		commit('CHANGE_ITEM',[...state.car])
	},
	removeItem:({commit,state},payload)=>{
		state.car.forEach((item,index)=>{
			if(item.id==payload.id){
				state.car.splice(index,1)
			}
		})
		commit('REMOVE_ITEM',[...state.car])
	},
	clearBuycar:({commit,state})=>commit('CLEAR_BUYCAR',[])
}
const mutations={
	ADD_ITEM:(state,payload)=>{
		state.car=payload
	},
	CHANGE_ITEM:(state,payload)=>{
		state.car=payload
	},
	REMOVE_ITEM:(state,payload)=>{
		state.car=payload
	},
	CLEAR_BUYCAR:(state,payload)=>{
		state.car=payload
	},
}
export default{
	namespaced:true,
	state,actions,mutations
}