<template>
	<div class="Car">
		<div class="car__flex">
			<div class="car__top">
				<span></span>
				<p>购物车</p>
				<span @click="edit">编辑</span>
			</div>
			<div class="car_content">
				<div v-if="$store.state.car.car.length==0">
					<div class="bg_icon"></div>
					<p class="car__word">您的购物车有点寂寞</p>
					<!-- <Go/> -->
					<router-link to='/home'><Go/></router-link>
				</div>
				<div class="car__control" v-for="(list,index) of car" :key="list.id" >
					<div class="car__circle" @click="add(index)"><span class="bgSpan" v-show="list.checked"></span></div>
					<img :src="list.img" alt="">	
					<div class="car__price">
						<h3>{{list.h3Title}}<span>爆款</span></h3>
						<p>八英寸<b class="car__b" v-show="error" @click="removeItem({id:list.id})">×</b></p>
						<a href="javascript:;" class="car__a">
							<span>￥{{list.price}}</span>
							<div class="car_button">
								<button @click="changeItem({id:list.id,num:-1})">-</button>
								<div>{{list.number}}</div>
								<button @click="changeItem({id:list.id,num:1})">+</button>
							</div>
						</a>
					</div>
				</div>
				<p class="guess">猜你喜欢</p>
				<div class="guess__cake">
					<Cake 
						v-for="(item,index) of data"
						  :key="item.id"
						  :data="item"
						  :index="index"
						  api-name="home"
					/>
				</div>
			</div>
		</div>
		<div v-for="(list,index) of car" :key="list.id" >
			<Settlement @click='setAdd(index)' :setRight='setRight' :list='car' :zongji='zongji' :jiesuan='jiesuan' />
		</div>
			
		
	</div>
</template>

<script>
	import Go from '../components/Go.vue'
	import Cake from '../components/Cake.vue'
	import Settlement from '../components/Settlement.vue'
	import {mapState,mapActions} from 'vuex'
	// import axios from 'axios'
	export default{
		data(){
			return {
				data:{},
				bgRight:false,
				setRight:false,
				error:false,
				zongji:0,
				jiesuan:0
			}
		},
		components:{
			Go,Cake,Settlement
		},
		mounted(){
			this.getCardata()
		},
		methods:{
			getCardata(){
				axios({
					url:'/data/detail.json',
				}).then(res=>{
					this.data = res.data
				})
			},
			...mapActions('car',['changeItem','clearBuycar','removeItem']),
			add(index){
				this.car[index].checked = !this.car[index].checked
				if(this.car[index].checked){	
					 this.zongji=parseInt(this.car[index].price)*parseInt(this.car[index].number)			
					 this.jiesuan=parseInt(this.car[index].number)
					 this.getTrue()
				}else{
					this.goodsComputed()
					this.setRight =false
				}
				this.car.filter(()=>{
					if(this.car[index].checked){
						this.goodsComputed()
						return;
					}
				})
			},
			getTrue(){
				let subCarArr = this.car.filter(el=>el.checked);
				if(subCarArr.length===this.car.length){
					this.setRight = !this.setRight
					return
				}
			},
			goodsComputed(){
				this.zongji=0;
				this.jiesuan=0;
				for(let i=0;i<this.car.length;i++){
					if(this.car[i].checked){
						 this.zongji+=parseInt(this.car[i].price)*parseInt(this.car[i].number)			
						 this.jiesuan+=parseInt(this.car[i].number)
					}
				}
			},
			setAdd(index){
				for(let i=0;i<this.car.length;i++){
					this.car[i].checked = !this.car[i].checked
				}
				this.setRight = !this.setRight;
				if(this.setRight){
					this.zongji=0
					this.jiesuan=0
					for(let i=0;i<this.car.length;i++){
						this.zongji+=parseInt(this.car[i].price)*parseInt(this.car[i].number)
						this.jiesuan+=parseInt(this.car[i].number)
					}
					this.setClick()
				}else{
					this.zongji=0
					this.jiesuan=0
				}
			},
			setClick(){
				for(let i = 0;i<this.car.length;i++){
					if(this.car[i].checked==false){
						this.car[i].checked=!this.car[i].checked;
						// this.setRight = !this.setRight;
					}
				}
			},
			edit(){
				this.error = !this.error;
			},
		},
		computed:mapState('car',['car'])
	}
</script>

<style>
	.car__top{
		width: 100%;
		height: .8rem;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	.car__top span{
		font-size: .24rem;
		color: #616161;
	}
	.car__top p{
		font-weight: bold;
		font-size: .26rem;
		color: #252525;
	}
	.car_content{
		background-color: #f8f8f8;
		overflow: hidden;
	}
	.bg_icon{
		margin:0 auto;
		width: 65%;
		height: 3.06rem;
		background: url(../../public/img/bg_icon.png) no-repeat top center;
		background-size: 100%;
	}
	.car__word{
		font-size: .22rem;
		color: #969696;
		text-align: center;
		margin-top: .26rem;
		margin-bottom: .45rem;
	}
	.guess{
		font-size: .28rem;
		color: #353535;
		margin-left: .3rem;
		margin-top: .9rem;
		margin-bottom: .27rem;
	}
	.guess__cake{
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
	.car__flex{
		flex: 1;
		overflow: auto;
	}
	.car__control{
		width: 90%;
		margin:0 auto;
		height: 2.85rem;
		background-color: white;
		border-radius: .1rem;
		margin-top: .3rem;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.car__control img{
		display: block;
		width: 1.9rem;
		height: 1.9rem;
	}
	.car_button{
		width: 1.6rem;
		height: .6rem;
		border: 1px solid #818181;
		border-radius: .1rem;
		display: flex;
	}
	.car_button div{
		width: .7rem;
		height: .6rem;
		border-left: 1px solid #818181;
		border-right: 1px solid #818181;
		text-align: center;
		line-height: .6rem;
		font-size: .2rem;
	}
	.car_button button{
		width: .8rem;
		height: .57rem;
		background-color: transparent;
		border: none;
		font-size: .2rem;
		outline: none;
	}
	.car__a{
		display: flex;
		align-items: center;
		margin-top: .3rem;
	}
	.car__a span{
		color: #ff363c;
		font-size: .24rem;
		display: block;
		margin-right: .5rem;
	}
	.car__price{
		margin-left: .2rem;
	}
	.car__price h3{
		font-size: .28rem;
	}
	.car__price p{
		font-size: .2rem;
		color: #a9a9a9;
		margin-top: .3rem;
	}
	.car__circle{
		width: .5rem;
		height: .5rem;
		border: 1px solid #cbcbcb;
		border-radius: 50%;
		margin-right: .3rem;
	}
	.car__price h3{
		display: flex;
		align-items: center;
	}
	.car__price h3 span{
		font-size: .22rem;
		color: #b04349;
		width: .8rem;
		height: .4rem;
		display: inline-block;
		border:1px solid #b04349;
		margin-left: .1rem;
		border-radius: .1rem;
		text-align: center;
		line-height: .4rem;
	}
	.bgSpan{
		display: block;
		width: 100%;
		height: 100%;
		background:url(/img/right.png);
		background-size: 100%;
		border-radius: 50%;
	}
	.car__b{
		font-size: .3rem;
		color: #cbcbcb;
		margin-left: 1.5rem;
	}
</style>
