<template>
	<div class="mine">
		<div class="mine__header" v-if="$store.state.user.data">
			<router-link tag="div" class="mine__login" to='/login'>
				<img src="../assets/images/logo.png" alt="">
				<b>{{$store.state.user.data.nikename}}   <p class="zhuxiao" @click="logout">注销</p></b>
			</router-link>
			<span class="iconfont icon-youjiantou"></span>
		</div>
		<Number/>
		<div class="mine__menu">
			<Menu/>
			<MineList/>
		</div>
		<div class="Mine__bottom">
			<Menu v-for="item in list" :key='item.id' :data='item' style="border-bottom: 1px solid #f6f6f6;" color='#343434' fontWeight='normal'/>
		</div>
		
	</div>
</template>

<script>
	import Number from '../components/Number.vue'
	import Menu from '../components/Menu.vue'
	import MineList from '../components/MineList.vue'
	import axios from 'axios'
	export default{
		data(){
			return{
				list:[
					{id:2,content:'修改个人信息',word:null,},
					{id:3,content:'收货人地址管理',word:null,},
					{id:4,content:'联系客服',word:null,}
				],
				user:{}
			}
		},
		beforeRouteEnter(to,from,next){
		      
		      // let local = window.localStorage.getItem('user')
		      
		      // if(!local){
		      //   next('/login')
		      //   return;
		      // }
		      
		      axios({
		        url:'/api/user',
		      }).then(
		        res=>{
		          if(res.data.err==0){
		            next()
		          }else{
		            next('/login')
		          }
		        }
		      )
		    },
		components:{
			Number,Menu,MineList
		},
		methods:{
			logout(){
				window.localStorage.removeItem('user')
				this.$store.commit('USER',{err:1})
				this.$router.push('/login')
			}
		}
	}
</script>

<style>
	.mine__header{
		width: 100%;
		height: 2.53rem;
		background-color: #fe3a3b;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.mine__login{
		display: flex;
		align-items: center;
	}
	.mine__header img{
		width: 1.53rem;
		height: 1.53rem;
		margin-left: .4rem;
		margin-right: .38rem;
	}
	.mine__header b{
		font-size: .34rem;
		color: #fff3ec;
	}
	.mine__header span{
		font-size: .44rem;
		color: #fff3ec;
		margin-right: .42rem;
	}
	.mine__menu{
		border-top: .2rem solid #fafafa;
		border-bottom: .2rem solid #fafafa;
	}
	.Mine__bottom{
		border-bottom: .2rem solid #fafafa;
		margin-bottom: 4rem;
	}
	.zhuxiao{
		font-size: .3rem;
	}
</style>
